@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#app{font-family:'Spartan', sans-serif;color:black;background-image:url(/brandonrowe-portfolio-v2/static/media/textured-paper.2e582823.png);position:relative;width:100%}#app .hero-component{display:grid;grid-template:1;justify-content:center;align-content:center;position:relative;width:100%;height:100vh}@media screen and (min-width: 600px){#app .hero-component{grid-template:repeat(3, 30%)/repeat(3, 30%)}}#app .hero-component>*{align-self:center;justify-self:center}#app .hero-component>*:not(.background-video){z-index:2}#app .hero-component .home-content{color:white;text-align:center}#app .hero-component .home-content a{color:#e0ffff}@media screen and (min-width: 600px){#app .hero-component .home-content{color:black;text-align:left}#app .hero-component .home-content a{color:navy}}#app .hero-component .about-me-content{display:flex;flex-direction:column;align-items:center;justify-content:space-between}#app .hero-component .about-me-content img{width:50%}#app .hero-component .about-me-content .about-me-text{padding:2rem}@media screen and (min-width: 600px){#app .hero-component .about-me-content{flex-direction:row;min-width:50rem}#app .hero-component .about-me-content img{width:75%}#app .hero-component .about-me-content .about-me-text{text-align:end;margin-left:5rem}}#app .hero-component .background-video{position:absolute;width:100%;height:100%;object-fit:cover}#app .links-component a{color:#e0ffff;opacity:0.6;text-decoration:none;padding-right:0.75rem}#app .links-component a svg{width:1.5rem;height:1.5rem}#app .links-component a:hover{opacity:1}#app .links-component a:hover svg{transform:scale(1.5);transition:transform 0.25s}#app footer{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:7rem;background:linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.6));color:white}#app .headshot{border-radius:500px}#app .copyright{margin:0;padding:1rem 0;opacity:.8}#app .carousel{display:flex;flex-direction:row}#app .grid-left{grid-column:1}#app .grid-right{grid-column:3}#app .grid-h-center{grid-column:2}#app .grid-top{grid-row:1}#app .grid-bottom{grid-row:3}#app .grid-v-center{grid-row:2}

